.seasons{
display: flex;
flex-direction:row;
overflow: hidden;
// max-width:360px;

@media (min-width:  $screen-xs) {
background-color:#eaeaec;
height: 330px;
// max-width:768px;
}

@media (min-width:  $screen-sm) {
background-color: white;
height: 530px;
// max-width:1200px;
}
}

.seasons_container{
  margin:auto;
position: center;
}

.seasons__map{
display: none;

@media (min-width:  $screen-xs) {
display: block;
background-image: url(../img/map.jpg);
flex-schrink: 1;
background-size: cover;
height: 330px;
margin-left: 10px;
width: 50%;
min-width: 50px;

}
@media (min-width:  $screen-sm) {
display: block;
background-image: url(../img/map.jpg);
flex-schrink: 1;
background-size: cover;
height: 530px;
margin-left: 10px;
width: 50%;
margin-left: 55px
}
}

.seasons__mobile_map{
background-image: url(../img/map.jpg);
background-size: cover;
height: 230px;
width: 100%;
margin-top: 50px;

@media (min-width:  $screen-xs) {
display: none
}
}

.season__description{
font-family: $Ubuntu;
margin-left: 0;

@media (min-width:  $screen-xs) {
width: 50%;
min-width: 250px;
}
@media (min-width:  $screen-sm) {
min-width: 330px;
height: 330px;
min-width: 200px;
}
}

.season__title{
font-family: $Jura;

display: block;
font-weight: 300;
font-size: 20px;
padding-top: 30px;
padding-left: 10px;

@media (min-width:  $screen-xs ) {
font-weight: 300;
font-size: 20px;
padding-top: 40px;
padding-left: 0px;
margin-left: 10px;
}
@media (min-width:  $screen-sm ) {

font-weight: 300;
font-size: 30px;
padding-top: 60px;
padding-left: 0px;
margin-left: 35px;
}
}

.season__legend{
position: relative;
top: 150px;
left: 10px;


@media (min-width:  $screen-xs ) {
left: 10px;
top: 40px;
min-width: 150px;
}
@media (min-width:  $screen-sm ) {
left: 35px;
top: 60px;
max-width: 340px;
}
}

.season__legend_clarification{

 &--red{
  margin-left: 10px;
  margin-right: 15px;
  font-style: $Jura;
  font-size: 10px;
  @media (min-width:  $screen-xs ) {
  font-size: 10px;
  }
  @media (min-width:  $screen-sm ) {
  font-size: 16px;
}
 }
  &--red:before{
  content: '';
  display: inline-block;
  height: 10px;
  width: 10px;
  background-color: #ef8989;
  position: relative;
  left: -10px;
  }


&--blue{
  margin-right: 15px;
  font-style: $Jura;
  font-size: 10px;
  @media (min-width:  $screen-xs ) {
  font-size: 10px;
  }
  @media (min-width:  $screen-sm ) {
  font-size: 16px;
}
}
&--blue:before{
  content: '';
  display: inline-block;
  height: 10px;
  width: 10px;
  background-color: #04a8a8;
  position: relative;
  left: -10px;
  }


&--yellow{
  margin-right: 15px;
  font-style: $Jura;
  font-size: 10px;
  @media (min-width:  $screen-xs ) {
  font-size: 10px;
  }
  @media (min-width:  $screen-sm ) {
  font-size: 16px;
  margin-right: 15px;
}
}
&--yellow:before{
  content: '';
  display: inline-block;
  height: 10px;
  width: 10px;
  background-color: #cdd63f;
  position: relative;
  left: -10px;
  }


&--orange{
  font-style: $Jura;
  font-size: 10px;
  @media (min-width:  $screen-xs ) {
  font-size: 10px;
  }
  @media (min-width:  $screen-sm ) {
  font-size: 16px;
  margin-right: 20px;}
}


&--orange:before{
  content: '';
  display: inline-block;
  height: 10px;
  width: 10px;
  background-color: #efb672;
  position: relative;
  left: -10px;
  }
}

.season__comment{
line-height:1.5;
font-size: 12px;
margin-left: 10px;

@media (min-width:  $screen-xs ) {
font-size: 12px;
margin-left: 10px;
margin-top: 45px;
line-height: 1.5;
max-width: 70%;
}

@media (min-width:  $screen-sm ) {
font-size: 16px;
margin-left: 35px;
margin-top: 80px;
width: 50%;
}
}